@import url(https://fonts.googleapis.com/css2?family=Istok+Web&family=Poppins&family=Salsa&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&family=Salsa&display=swap);
*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --primary-color: #192538;
  --dark-blue: #0e141f;
  --secondary-color: #e24561;
  --grey: #5a6d86;
  --poppins: "Poppins", sans-serif;
  --istok-web: "Istok Web", sans-serif;
  --salsa: "Salsa", cursive;
}

html {
  font-size: 10px;
}

body {
  background-color: #192538;
  background-color: var(--primary-color);
  height: 100vh;
  width: 100vw;
  color: #fff;
  overflow-x: hidden;
}

.section {
  height: 100vh;
  padding: 4rem 16rem 0 40rem;
}

@media screen and (max-width: 600px) {
  .section {
    padding: 0;
    margin-top: 8rem;
  }
}

.sidebar {
  position: fixed;
  width: 25rem;
  height: 100%;
  background-color: var(--dark-blue);
  top: 0;
  left: 0;
  z-index: 9999;
}

.logo {
  padding: 5rem 0 0 0;
  text-align: center;
}

.logo p {
  color: #fff;
  font-family: var(--salsa);
  font-size: 1.7rem;
  letter-spacing: 0.2rem;
  margin-top: 1rem;
}

.menu {
  display: table;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
}

.menu-middle {
  display: table-cell;
  vertical-align: middle;
  position: relative;
}

.menu-container {
  margin: 0 3rem 0 0;
  position: relative;
}

.menu-container ul {
  list-style: none;
}

.menu-container ul li {
  padding: 0 1rem 1.5rem 1.3rem;
  position: relative;
}

.menu-container ul li .menu-link {
  color: var(--grey);
  font-family: var(--poppins);
  font-size: 1.5rem;
  text-decoration: none;
  line-height: 4rem;
  display: block;
  position: relative;
  margin-left: 8rem;
  padding-left: 0.5rem;
  cursor: pointer;
}

.menu-container ul li .menu-link:hover {
  color: #fff;
}

.menu-container ul li .menu-link.active {
  color: #fff;
}

.menu-container ul li .menu-link .circle {
  background-color: var(--dark-blue);
  height: 1.5rem;
  width: 1.5rem;
  border: 0.2rem solid var(--grey);
  border-radius: 100%;
  margin-right: 1rem;
  display: block;
  position: absolute;
  left: -2rem;
  top: 1.2rem;
  z-index: 2;
}

.menu-container ul li .menu-link.active > .circle {
  border-color: var(--secondary-color);
  transition: all 0.2s;
}
.menu-container ul li .menu-link .line {
  background-color: var(--secondary-color);
  height: 1.3rem;
  width: 0rem;
  border-radius: 5rem;
  margin-right: 1rem;
  position: absolute;
  right: -6.5rem;
  top: 1.25rem;
  display: block;
}

.menu-container ul li .menu-link.active > .line {
  width: 3.5rem;
  transition: all 0.2s;
}
.menu-container .border {
  position: absolute;
  height: 16rem;
  width: 0.2rem;
  top: 2rem;
  left: 8rem;
  z-index: 1;
  background-color: var(--grey);
}

@media screen and (max-width: 600px) {
  .sidebar {
    display: none;
  }
}

#about {
  display: flex;
  align-items: center;
  justify-content: center;
}

#about .intro p {
  font-size: 1.8rem;
  font-family: var(--istok-web);
  letter-spacing: -0.01rem;
  margin-bottom: 3rem;
}

#about .profile-pic {
  height: 40rem;
}

.resume-button {
  background-color: var(--secondary-color);
  color: #fff;
  padding: 1.5rem 4rem;
  border-radius: 5rem;
  border: none;
  font-family: var(--poppins);
  font-size: 1.3rem;
  cursor: pointer;
  text-decoration: none;
  position: relative;
}

.resume-button:hover {
  opacity: 0.8;
  transition: opacity 0.2s;
}

.right-arrow {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  font-size: 2rem;
  margin-left: 0.5rem;
}

.resume-button:hover .right-arrow {
  transform: translateX(1rem);
}

#petal,
#stem {
  -webkit-animation: fade 2s infinite alternate ease-in-out;
          animation: fade 2s infinite alternate ease-in-out;
}

#petal:nth-of-type(2n + 1) {
  -webkit-animation-delay: 3s;
          animation-delay: 3s;
}

#petal:nth-of-type(2n) {
  -webkit-animation-delay: 4s;
          animation-delay: 4s;
}

@-webkit-keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.2;
  }
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.2;
  }
}

@media screen and (max-width: 600px) {
  #about {
    flex-wrap: wrap;
    padding: 2rem 2rem 0 2rem;
  }
  #about .intro p {
    font-size: 1.6rem;
  }
}

@media screen and (max-width: 900px) and (min-width: 601px) {
  #about {
    flex-wrap: wrap;
    padding: 4rem 12rem 0 30rem;
  }
}

@media screen and (max-width: 1000px) and (min-width: 901px) {
  #about {
    padding: 4rem 10rem 0 30rem;
  }
}

#skills {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.skillset {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  justify-content: center;
}

.skill-item {
  height: 30rem;
  width: 30rem;
  position: relative;
}

.skillset .svg {
  height: 100%;
  width: 100%;
  transform: rotate(-90deg);
}

.skillset .svg circle {
  fill: var(--dark-blue);
  stroke-width: 2rem;
}

.skillset .svg circle:nth-child(1) {
  stroke: var(--dark-blue);
}

.skillset .svg circle:nth-child(2) {
  fill: none;
  stroke: var(--secondary-color);
  stroke-linecap: round;
  /* animation: showprogress 1s linear; */
}

/* @keyframes showprogress {
  0% {
    stroke-dashoffset: 314;
  }
  100% {
    stroke-dashoffset: 0;
  }
} */

.skillset .svg circle:nth-child(3) {
  fill: var(--primary-color);
}

.skill-name {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.skill-name h3 {
  font-size: 2rem;
  font-family: var(--istok-web);
  letter-spacing: 0.1rem;
}

@media screen and (max-width: 1060px) {
  .skillset {
    grid-template-columns: repeat(2, 1fr);
  }
  #skills {
    margin-top: 20rem;
  }
}

@media screen and (max-width: 700px) and (min-width: 599px) {
  .skillset {
    grid-template-columns: repeat(1, 1fr);
  }
  #skills {
    margin-top: 10rem;
    padding-top: 90rem;
  }
}

@media screen and (max-width: 600px) and (min-width: 540px) {
  .skillset {
    grid-template-columns: repeat(2, 1fr);
  }
  #skills {
    padding: 3rem 0;
    margin-top: 20rem;
  }
}

@media screen and (max-width: 541px) {
  .skillset {
    grid-template-columns: repeat(1, 1fr);
  }
  #skills {
    padding: 3rem 0;
    margin-top: 70rem;
  }
}

#projects {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.projectset {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  justify-content: space-around;
  grid-gap: 1.5rem;
}
.project-item {
  height: 30rem;
  width: 25rem;
  border-radius: 0.5rem;
  margin: 1.5rem auto;
  padding: 0 1rem;
  position: relative;
}

.project-item-inner {
  height: 100%;
  width: 100%;
  background-color: var(--dark-blue);
  border-radius: inherit;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0 1.5rem;
}

.project-item-inner .project-name {
  font-size: 2.5rem;
  font-family: var(--istok-web);
  text-align: center;
  line-height: 2.5rem;
  margin-bottom: 1.5rem;
}

.project-item-inner .demo,
.project-item-inner .source-code {
  text-decoration: none;
  color: #fff;
  background-color: var(--grey);
  font-family: var(--poppins);
  font-size: 1.3rem;
  padding: 1rem 3.1rem;
  border-radius: 5rem;
}

.project-item-inner .demo {
  padding: 1rem 4rem;
  margin: 1rem auto;
}

.project-item-inner .demo:hover,
.project-item-inner .source-code:hover {
  opacity: 0.8;
  transition: opacity 0.2s;
}

.project-item-outer {
  height: 100%;
  width: 100%;
  background-color: var(--secondary-color);
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: inherit;
  box-shadow: 0 -1rem 1.5rem rgba(0, 0, 0, 0.4) inset;
  cursor: pointer;
}

.project-item-outer .project-icon {
  font-size: 5rem;
}

.project-item:hover .project-item-outer {
  height: 0;
  background-color: transparent;
  transition: all 0.5s;
}

.project-item:hover .project-item-outer .project-icon {
  opacity: 0;
}

.source-code,
.demo {
  cursor: pointer;
}

@media screen and (max-width: 900px) {
  .projectset {
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}

@media screen and (max-width: 1050px) {
  .projectset {
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
  #projects {
    padding-top: 40rem;
  }
}
@media screen and (max-width: 770px) {
  .projectset {
    grid-template-columns: repeat(1, 1fr);
    align-items: center;
  }
  #projects {
    margin-top: 30rem;
  }
}

@media screen and (max-width: 500px) {
  #projects {
    padding-top: 100rem;
  }
}

#contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.contact-container {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#contact-word {
  fill: none;
  stroke: var(--grey);
  stroke-opacity: 0.5;
  -webkit-animation: animateText 4s infinite ease;
          animation: animateText 4s infinite ease;
  stroke-dasharray: 10rem;
  opacity: 0;
}

@-webkit-keyframes animateText {
  0% {
    stroke-dashoffset: 10rem;
    opacity: 1;
  }
  100% {
    stroke-dashoffset: 0rem;
    opacity: 1;
  }
}

@keyframes animateText {
  0% {
    stroke-dashoffset: 10rem;
    opacity: 1;
  }
  100% {
    stroke-dashoffset: 0rem;
    opacity: 1;
  }
}

.contact-svg {
  position: relative;
  height: 10rem;
  width: 40rem;
  margin-bottom: 4rem;
}
.contact-svg svg {
  height: 100%;
  width: 100%;
}
.contact-us {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-family: var(--istok-web);
  font-size: 2rem;
}

@media screen and (max-width: 500px) {
  #contact {
    margin: 50rem auto -10rem auto;
  }
  .contact-svg {
    height: 6rem;
    width: 24rem;
  }
  .contact-us {
    font-size: 1.7rem;
  }
  #contact-word {
    stroke-dasharray: 6rem;
  }
}

@media screen and (max-width: 800px) and (min-width: 501px) {
  #contact {
    margin: 30rem auto -10rem auto;
  }
  .contact-svg {
    height: 8rem;
    width: 32rem;
  }
  #contact-word {
    stroke-dasharray: 8rem;
  }
  @-webkit-keyframes animateText {
    0% {
      stroke-dashoffset: 8rem;
      opacity: 1;
    }
    100% {
      stroke-dashoffset: 0rem;
      opacity: 1;
    }
  }
  @keyframes animateText {
    0% {
      stroke-dashoffset: 8rem;
      opacity: 1;
    }
    100% {
      stroke-dashoffset: 0rem;
      opacity: 1;
    }
  }
}

@media screen and (max-width: 1040px) and (min-width: 801px) {
  #contact {
    margin-top: 10rem;
  }
}

.section.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-family: var(--poppins);
  font-size: 1.5rem;
  color: var(--grey);
  width: 100%;
  height: 7rem;
}

.section.footer span {
  color: #fff;
}

@media screen and (max-width: 770px) {
  .section.footer {
    font-size: 1.2rem;
  }
}

.header {
  position: fixed;
  background-color: var(--dark-blue);
  top: 0;
  left: 0;
  height: 6rem;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 20;
  padding: 0 2rem;
}

#av-logo {
  height: 3.5rem;
  width: 3.5rem;
}

.mob-menu-icon {
  font-size: 2.3rem;
  cursor: pointer;
  z-index: 9999999;
  color: var(--secondary-color);
}

.mob-menu-icon:hover {
  color: var(--grey);
}
.mob-nav {
  background-color: var(--dark-blue);
  display: flex;
  position: absolute;
  top: 0rem;
  right: 0;
  width: 25rem;
  height: 100vh;
  transition: all 0.3s ease-in-out;
}

.mob-nav.hide {
  right: -25rem;
}

@media screen and (min-width: 600px) {
  .header {
    display: none;
  }
}

.form-section {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgb(236, 179, 179);
  position: relative;
}

.go-back {
  color: rgb(34, 34, 34);
  font-family: var(--poppins);
  font-size: 1.5rem;
  padding: 1rem 0 0 1rem;
}

.go-back:hover {
  color: var(--secondary-color);
}

iframe {
  color: var(--secondary-color);
  font-size: 3rem;
  display: block;
  overflow-y: auto;
  overflow-x: hidden;
  background-color: rgb(236, 179, 179);
}

@media screen and (max-width: 600px) {
  iframe {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    padding-top: 4rem;
  }
  .go-back {
    position: absolute;
    top: 0;
    z-index: 10;
  }
}

